*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar {
  width: 100vw;
  position: absolute;
  font-family: "Barlow", sans-serif;
  font-size: 18px;
  color: hsl(0, 0%, 100%);
  padding: 1.2rem 1.6rem;
}
.navbar__burger {
  display: none;
}
.navbar__logo {
  margin-right: auto;
}
.navbar__links a {
  padding-right: 3em;
}
.navbar__btn {
  border: none;
  border-radius: 18px;
  padding: 0.6em 1.4em;
  background-color: hsl(0, 0%, 100%);
  color: hsl(167, 40%, 24%);
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}
.navbar__btn:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
  color: hsl(0, 0%, 100%);
}
@media (max-width: 800px) {
  .navbar__links, .navbar__btn {
    display: none;
  }
  .navbar__burger {
    display: inline-block;
  }
}

.hero {
  height: 100vh;
  background-image: url("../images/desktop/image-header.jpg");
  background-size: cover;
  background-position: bottom;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.hero__title h1 {
  font-family: "Fraunces", serif;
  font-size: 3rem;
  text-transform: uppercase;
  color: hsl(0, 0%, 100%);
}